/* styles.css */

/* Reset default browser styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    padding: 30px;
    color: #ecf0f1;
    box-shadow: 0 1px 30px rgba(0,0,0,0.3);
}

body {
    background-color: #1c1c1c;
    font-family: 'Roboto', sans-serif;
}

.record-player {
    width: 100%;
    max-width: 1200px;
    position: relative;
    padding: 20px;
    margin: auto;
    margin-top: 50px;
}

.base {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #2a2a2a; /* Slightly lighter than body for contrast */
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0,0,0,0.9);
    padding: 20px;
    gap: 20px;
}

.turntable-container {
    position: relative;
    width: 250px;
    height: 250px;
}

.turntable {
    position: relative;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 50% 50%, #444, #222);
    border: 10px solid #555;
    box-shadow: inset 0 0 10px rgba(0,0,0,0.8);
    overflow: visible;
    transition: transform 0.5s ease-in-out;
}

.platter {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80%;
    height: 80%;
    background: #333;
    border-radius: 50%;
    border: 10px solid #555;
    transform: translate(-50%, -50%);
}

.record {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70%;
    height: 70%;
    background: radial-gradient(circle, #0004 60%, #555);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    animation: spin 5s linear infinite;
    transition: animation-duration 0.5s;
    z-index: 1;
    box-shadow: 0 0 15px rgba(0,0,0,0.5);
    overflow: hidden;
}

/* Grooves for Rotation Visibility */
.grooves {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: repeating-conic-gradient( 
        #0000 0deg 4deg, 
        #0002 160deg 4deg );
    opacity: 0.4;
    border-radius: 50%;
    animation: spinGrooves 5s linear infinite;
    pointer-events: none;
}

@keyframes spinGrooves {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

.record .label {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30%;
    height: 30%;
    background: radial-gradient(circle, #ff5733 40%, #c0392b);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    box-shadow: inset 0 0 5px rgba(0,0,0,0.3);
}

.grooves::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: repeating-linear-gradient(
        0deg,
        transparent,
        transparent 9px,
        #555 10px
    );
    border-radius: 50%;
    opacity: 0.1;
}

.tonearm {
    position: absolute;
    top: 10%;
    width: 150px;
    height: 6px;
    background: #777;
    border-radius: 3px;
    transform: rotate(90deg);
    transform-origin: 0% 50%;
    transition: transform 1s ease-in-out;
    z-index: 2;
    left: 5%;
}

.needle {
    position: absolute;
    top: -3px;
    right: -10px;
    width: 25px;
    height: 4px;
    background: #bdc3c7;
    border-radius: 2px;
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
}

/* Controls Styling */
.controls {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    width: 100%;
    flex: 1;
}

.control-group {
    display: flex;
    align-items: center;
    gap: 15px;
    flex-wrap: wrap;
    width: 100%;
}

button, select, #volume-control {
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
    width: 100%;
    gap: 10px;
}

button {
    background-color: #e74c3c;
    color: #fff;
    display: flex;
    align-items: center;
    flex: 1;
}

button:hover {
    background-color: #c0392b;
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(231,76,60,0.4);
}

select {
    background-color: #333;
    color: #ecf0f1;
    padding: 10px;
    border-radius: 8px;
    font-size: 16px;
}

.volume-label {
    color: #ecf0f1;
    font-size: 16px;
    font-weight: 500;
    display: contents;
}

#volume-control {
    width: 200px;
    background: #555;
    height: 6px;
    border-radius: 3px;
    outline: none;
    transition: background 0.3s;
    flex: 1;
    padding: 0;
}

#volume-control::-webkit-slider-thumb, #volume-control::-moz-range-thumb {
    background-color: #e74c3c;
    border: none;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
}

#volume-control::-webkit-slider-thumb:hover, #volume-control::-moz-range-thumb:hover {
    background-color: #c0392b;
    transform: scale(1.2);
}

/* Progress Bar */
.progress-bar {
    width: 100%;
    height: 8px;
    background-color: #555;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
}

.progress {
    width: 0%;
    height: 100%;
    background-color: #e74c3c;
    transition: width 0.1s linear;
}

#track-name {
    font-size: 14px;
    color: #ecf0f1;
    text-align: center;
    word-wrap: break-word;
}

/* Queue Section */
.queue-dropzone {
    width: 100%;
    padding: 15px;
    border: 2px dashed #e74c3c;
    border-radius: 8px;
    background-color: rgba(231, 76, 60, 0.1);
    color: #ecf0f1;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s, border-color 0.3s;
}

.queue-dropzone.dragover {
    background-color: rgba(231, 76, 60, 0.2);
    border-color: #c0392b;
}

.queue-list {
    list-style: none;
    width: 100%;
    max-height: 100px;
    overflow-y: auto;
    margin-top: 10px;
    padding: 0;
}

.queue-list li {
    background-color: #444;
    color: #ecf0f1;
    padding: 8px 12px;
    margin-bottom: 8px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    transition: background-color 0.3s;
}

.queue-list li:hover {
    background-color: #555;
}

.queue-list li p {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 10;
}

.remove-btn {
    background: none;
    border: none;
    color: #e74c3c;
    cursor: pointer;
    font-size: 12px;
    margin-left: 10px;
    transition: color 0.3s;
    justify-content: center;
    flex: 1;
}

#visualizer {
    background-color: #333;
    border: 1px solid #555;
    border-radius: 5px;
    width: 100%;
    height: 100px;
    box-shadow: inset 0 0 10px rgba(0,0,0,0.5);
}

/* Progress Container */
.progress-container {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
}

.time {
    color: #ecf0f1;
    font-size: 14px;
    min-width: 40px;
    text-align: center;
}

.progress-bar {
    position: relative;
    flex: 1;
    height: 8px;
    background-color: #555;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;
}

.progress-bar:hover {
    background-color: #666;
}

/* Improved Spin Animation */
@keyframes spin {
    from { transform: translate(-50%, -50%) rotate(0deg); }
    to { transform: translate(-50%, -50%) rotate(360deg); }
}

/* Paused State for Record */
.paused {
    animation-play-state: paused;
}

/* Playing State for Tonearm */
.playing .tonearm {
    transform: rotate(60deg);
}

.paused .tonearm {
    transform: rotate(90deg);
}

/* Drag-and-Drop Highlight */
#turntable-dropzone.dragover {
    border: 2px dashed #e74c3c;
    background-color: rgba(231, 76, 60, 0.1);
}

.now-playing {
    width: 100%;
    text-align: center;
    color: #ecf0f1;
    font-size: 16px;
    font-weight: 700;
}

/* Responsive Design Adjustments */
@media (max-width: 600px) {
    .progress-container {
        flex-direction: column;
        align-items: stretch;
    }

    .time {
        text-align: left;
    }
}

@media (min-width: 800px) {
    .turntable-container {
        width: 300px;
        height: 300px;
    }

    .tonearm {
        width: 200px;
    }

    #visualizer {
        height: 150px;
    }

    .queue-list {
        max-height: 150px;
    }
}

@media (min-width: 1100px) {
    .base {
        flex-direction: row;
        justify-content: space-between;
        padding: 40px;
        gap: 40px;
    }

    .turntable-container {
        width: 400px;
        height: 400px;
    }

    .tonearm {
        width: 250px;
    }
}


/* Styles for YouTube URL Input */
.youtube-input {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
    width: 100%;
}

#youtube-url-input {
    flex: 1;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

#add-youtube-url {
    padding: 8px 12px;
    background-color: #e74c3c;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

#add-youtube-url:hover {
    background-color: #c0392b;
}

#skip, #go-back {
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    background-color: #444;
    color: #fff;
    border: none;
    border-radius: 8px;
    text-wrap: nowrap;
}

#skip:hover, #go-back:hover {
    background-color: #555;
}

/* Style for dragging items */
.queue-list li.dragging {
    opacity: 0.5;
    border: 2px dashed #e74c3c;
}

/* Style for the queue list */
.queue-list {
    list-style: none;
    padding: 0;
}

.queue-list li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #444;
    cursor: move; /* Indicate draggable */
    background-color: #333;
}

.queue-list li:hover {
    background-color: #555;
}

.remove-btn {
    background-color: #e74c3c;
    border: none;
    color: white;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 3px;
}

.remove-btn:hover {
    background-color: #c0392b;
}