* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header {
  color: #ecf0f1;
  padding: 30px;
  box-shadow: 0 1px 30px #0000004d;
}

body {
  background-color: #1c1c1c;
  font-family: Roboto, sans-serif;
}

.record-player {
  width: 100%;
  max-width: 1200px;
  margin: 50px auto auto;
  padding: 20px;
  position: relative;
}

.base {
  background: #2a2a2a;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 10px 30px #000000e6;
}

.turntable-container {
  width: 250px;
  height: 250px;
  position: relative;
}

.turntable {
  background: radial-gradient(circle, #444, #222);
  border: 10px solid #555;
  width: 100%;
  height: 100%;
  transition: transform .5s ease-in-out;
  position: relative;
  overflow: visible;
  box-shadow: inset 0 0 10px #000c;
}

.platter {
  background: #333;
  border: 10px solid #555;
  border-radius: 50%;
  width: 80%;
  height: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.record {
  z-index: 1;
  background: radial-gradient(circle, #0004 60%, #555);
  border-radius: 50%;
  width: 70%;
  height: 70%;
  transition: animation-duration .5s;
  animation: 5s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px #00000080;
}

.grooves {
  opacity: .4;
  pointer-events: none;
  background: repeating-conic-gradient(#0000 0deg 4deg, #0002 160deg 4deg);
  border-radius: 50%;
  width: 100%;
  height: 100%;
  animation: 5s linear infinite spinGrooves;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes spinGrooves {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.record .label {
  background: radial-gradient(circle, #ff5733 40%, #c0392b);
  border-radius: 50%;
  width: 30%;
  height: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 5px #0000004d;
}

.grooves:before {
  content: "";
  opacity: .1;
  background: repeating-linear-gradient(0deg, #0000, #0000 9px, #555 10px);
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.tonearm {
  transform-origin: 0%;
  z-index: 2;
  background: #777;
  border-radius: 3px;
  width: 150px;
  height: 6px;
  transition: transform 1s ease-in-out;
  position: absolute;
  top: 10%;
  left: 5%;
  transform: rotate(90deg);
}

.needle {
  background: #bdc3c7;
  border-radius: 2px;
  width: 25px;
  height: 4px;
  position: absolute;
  top: -3px;
  right: -10px;
  box-shadow: 0 0 5px #0000004d;
}

.controls {
  flex-direction: column;
  flex: 1;
  justify-content: center;
  gap: 20px;
  width: 100%;
  display: flex;
}

.control-group {
  flex-wrap: wrap;
  align-items: center;
  gap: 15px;
  width: 100%;
  display: flex;
}

button, select, #volume-control {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  gap: 10px;
  width: 100%;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
  transition: background-color .3s, transform .2s, box-shadow .3s;
}

button {
  color: #fff;
  background-color: #e74c3c;
  flex: 1;
  align-items: center;
  display: flex;
}

button:hover {
  background-color: #c0392b;
  transform: scale(1.05);
  box-shadow: 0 4px 15px #e74c3c66;
}

select {
  color: #ecf0f1;
  background-color: #333;
  border-radius: 8px;
  padding: 10px;
  font-size: 16px;
}

.volume-label {
  color: #ecf0f1;
  font-size: 16px;
  font-weight: 500;
  display: contents;
}

#volume-control {
  background: #555;
  border-radius: 3px;
  outline: none;
  flex: 1;
  width: 200px;
  height: 6px;
  padding: 0;
  transition: background .3s;
}

#volume-control::-webkit-slider-thumb {
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: background-color .3s, transform .2s;
}

#volume-control::-moz-range-thumb {
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: background-color .3s, transform .2s;
}

#volume-control::-webkit-slider-thumb:hover {
  background-color: #c0392b;
  transform: scale(1.2);
}

#volume-control::-moz-range-thumb:hover {
  background-color: #c0392b;
  transform: scale(1.2);
}

.progress-bar {
  background-color: #555;
  border-radius: 4px;
  width: 100%;
  height: 8px;
  position: relative;
  overflow: hidden;
}

.progress {
  background-color: #e74c3c;
  width: 0%;
  height: 100%;
  transition: width .1s linear;
}

#track-name {
  color: #ecf0f1;
  text-align: center;
  word-wrap: break-word;
  font-size: 14px;
}

.queue-dropzone {
  color: #ecf0f1;
  text-align: center;
  cursor: pointer;
  background-color: #e74c3c1a;
  border: 2px dashed #e74c3c;
  border-radius: 8px;
  width: 100%;
  padding: 15px;
  transition: background-color .3s, border-color .3s;
}

.queue-dropzone.dragover {
  background-color: #e74c3c33;
  border-color: #c0392b;
}

.queue-list {
  width: 100%;
  max-height: 100px;
  margin-top: 10px;
  padding: 0;
  list-style: none;
  overflow-y: auto;
}

.queue-list li {
  color: #ecf0f1;
  background-color: #444;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  padding: 8px 12px;
  font-size: 14px;
  transition: background-color .3s;
  display: flex;
}

.queue-list li p {
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 10;
  margin: 0;
  overflow: hidden;
}

.remove-btn {
  color: #e74c3c;
  cursor: pointer;
  background: none;
  border: none;
  flex: 1;
  justify-content: center;
  margin-left: 10px;
  font-size: 12px;
  transition: color .3s;
}

#visualizer {
  background-color: #333;
  border: 1px solid #555;
  border-radius: 5px;
  width: 100%;
  height: 100px;
  box-shadow: inset 0 0 10px #00000080;
}

.progress-container {
  align-items: center;
  gap: 10px;
  width: 100%;
  display: flex;
}

.time {
  color: #ecf0f1;
  text-align: center;
  min-width: 40px;
  font-size: 14px;
}

.progress-bar {
  cursor: pointer;
  background-color: #555;
  border-radius: 4px;
  flex: 1;
  height: 8px;
  position: relative;
  overflow: hidden;
}

.progress-bar:hover {
  background-color: #666;
}

@keyframes spin {
  from {
    transform: translate(-50%, -50%)rotate(0);
  }

  to {
    transform: translate(-50%, -50%)rotate(360deg);
  }
}

.paused {
  animation-play-state: paused;
}

.playing .tonearm {
  transform: rotate(60deg);
}

.paused .tonearm {
  transform: rotate(90deg);
}

#turntable-dropzone.dragover {
  background-color: #e74c3c1a;
  border: 2px dashed #e74c3c;
}

.now-playing {
  text-align: center;
  color: #ecf0f1;
  width: 100%;
  font-size: 16px;
  font-weight: 700;
}

@media (width <= 600px) {
  .progress-container {
    flex-direction: column;
    align-items: stretch;
  }

  .time {
    text-align: left;
  }
}

@media (width >= 800px) {
  .turntable-container {
    width: 300px;
    height: 300px;
  }

  .tonearm {
    width: 200px;
  }

  #visualizer {
    height: 150px;
  }

  .queue-list {
    max-height: 150px;
  }
}

@media (width >= 1100px) {
  .base {
    flex-direction: row;
    justify-content: space-between;
    gap: 40px;
    padding: 40px;
  }

  .turntable-container {
    width: 400px;
    height: 400px;
  }

  .tonearm {
    width: 250px;
  }
}

.youtube-input {
  gap: 10px;
  width: 100%;
  margin-bottom: 10px;
  display: flex;
}

#youtube-url-input {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  padding: 8px;
}

#add-youtube-url {
  color: #fff;
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
}

#add-youtube-url:hover {
  background-color: #c0392b;
}

#skip, #go-back {
  cursor: pointer;
  color: #fff;
  text-wrap: nowrap;
  background-color: #444;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 1rem;
}

#skip:hover, #go-back:hover {
  background-color: #555;
}

.queue-list li.dragging {
  opacity: .5;
  border: 2px dashed #e74c3c;
}

.queue-list {
  padding: 0;
  list-style: none;
}

.queue-list li {
  cursor: move;
  background-color: #333;
  border-bottom: 1px solid #444;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.queue-list li:hover {
  background-color: #555;
}

.remove-btn {
  color: #fff;
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
}

.remove-btn:hover {
  background-color: #c0392b;
}
